import { reactive } from "vue";
import Papa from "../../node_modules/papaparse";

const data = reactive({
  demo: "abc",
  trainingSetInputCsv: "",
  trainingSetInputJSON: "",
  trainingSetFileName: "",
  trainingSetFileSize: "",
  trainingSetFileType: "",
});

const ui = reactive({
  parsing: false,
  next: true,
  step: 0,
});

const parseOptions = reactive({
  header: true,
  dynamicTyping: true,
  delimiter: "",
  skipEmptyLines: true,
  worker: true,
});

let cold = { storage: "" };

const methods = {
  papaparse: function (input, options, target) {
    ui.parsing = true;
    Papa.parse(input, {
      ...options,
      complete: function (results) {
        ui.parsing = false;
        ui.next = true;
        data[target] = results;
        cold.storage = results;
      },
    });
  },
  modifyTypes: function (key, index, value) {
    console.log("muchas gracias");
    cold.storage.data[index][key] = value;
    data.trainingSetInputJSON.data[index][key] = value;
  },
};

export default {
  data,
  ui,
  methods,
  parseOptions,
  cold,
};
