<template>
  <div>
    <h2>HALLO</h2>
    <!-- <div class="content">
      <div class="columns">
        <div class="column has-text-centered is-4"> -->
    <div v-if="Object.keys(summary).length < 1">Loading your data...</div>
    <div v-else>
      <ul>
        <li v-for="(item, idx) in Object.entries(summary)" :key="idx">
          {{ item[0] }}
          <ul>
            <div
              class="columns"
              v-for="(type, idy) in Object.keys(item[1])"
              :key="idy"
            >
              <div class="column is-7">
                <li
                  :class="[
                    { 'green bold': typeSum[item[0]] == item[1][type] },
                    { 'red bold': typeSum[item[0]] > item[1][type] },
                  ]"
                >
                  {{
                    type == "object"
                      ? "Empty"
                      : type.charAt(0).toUpperCase() + type.slice(1)
                  }}
                  : {{ item[1][type] }}/{{
                    Object.values(item[1]).reduce((a, b) => a + b)
                  }}
                </li>
              </div>
              <div class="column is-5 has-text-right">
                <div class="select is-small">
                  <select v-model="selected[idx + '-' + idy]">
                    <option
                      v-for="(option, idz) in options"
                      :key="idz"
                      :value="{
                        key: item[0],
                        replace: type,
                        with: option.value,
                      }"
                    >
                      {{ option.text }}
                    </option>
                  </select>
                </div>
              </div>
            </div>
          </ul>
        </li>
      </ul>
      <div class="columns">
        <div class="column has-text-right">
          <button @click="applyChanges()" class="button is-primary">
            Apply
          </button>
        </div>
      </div>
      <!-- </div>
        </div>
      </div> -->
      {{ typeSum }}
    </div>
  </div>
</template>

<script>
import { reactive, toRefs, inject, computed, onMounted } from "vue";
export default {
  setup() {
    const store = inject("store");
    const types = reactive({
      summary: {},
    });

    //Dropdown to format types
    const dropdown = reactive({
      options: [
        { text: "--", value: false },
        { text: "Convert to string", value: 1 },
        { text: "Set to 0", value: 2 },
        { text: "Erase", value: 3 },
      ],
      selected: [],
    });

    //Formats types
    const applyChanges = function () {
      let settings = [];
      Object.keys(dropdown.selected).forEach((el) => {
        console.log(dropdown.selected[el].key);
        settings.push({
          key: dropdown.selected[el].key,
          replace: dropdown.selected[el].replace,
          with: dropdown.selected[el].with,
        });
        // formatTypes({
        //   key: dropdown.selected[el].key,
        //   with: dropdown.selected[el].with,
        // });
        // store.cold.storage.data.forEach((dat, idx) => {
        //   // console.log("dat:", dat.PassengerId, dropdown.selected[el].key);
        //   if (
        //     typeof dat[dropdown.selected[el].key] ===
        //     dropdown.selected[el].replace
        //   ) {
        //     // console.log("we need action");
        //     switch (dropdown.selected[el].with) {
        //       case 0:
        //         break;
        //       case 1:
        //         store.methods.modifyTypes(
        //           dropdown.selected[el].key,
        //           idx,
        //           dat[dropdown.selected[el].key].toString()
        //         );
        //         break;
        //       case 2:
        //         store.methods.modifyTypes(dropdown.selected[el].key, idx, 0);
        //         break;
        //       case 3:
        //         store.methods.modifyTypes(dropdown.selected[el].key, idx, "");
        //         break;
        //     }
        //   }
        // });
      });
      formatTypes(settings);
    };

    //Sends out a worker to do the main thread blocking computation of summing all typeof for every value in the parsed object.
    const sumTypeof = function () {
      if (window.Worker) {
        let myWorker = new Worker("/scripts/worker/sumTypeof.js", {
          type: "module",
        });
        console.log("Sending data to worker...");
        myWorker.postMessage({
          meta: [...store.data.trainingSetInputJSON.meta.fields],
          data: store.cold.storage.data,
        });
        myWorker.onmessage = function (e) {
          types.summary = e.data;
          store.ui.next = true;
        };
      } else {
        types.summary =
          "Workers not supported in your Browser. Please update your browser.";
      }
    };
    //Returns the sum of all types combined per Key. Example Numbers: 3, Objects: 2, this computes to 5. Use to display Number: 3/5, Object: 2/5.
    const typeSum = computed(() => {
      let results = {};
      Object.keys(types.summary).map((key) => {
        results[key] = Object.values(types.summary[key]).reduce((a, b) => {
          return a + b;
        });
      });
      return results;
    });

    onMounted(sumTypeof);

    //Format Types based on Dropdowns. Requires Worker.
    const formatTypes = function (key) {
      let myWorker = new Worker("/scripts/worker/formatTypes.js", {
        type: "module",
      });
      console.log("Sending data to worker n2...");
      myWorker.postMessage({
        data: store.cold.storage.data,
        replace: key,
      });
      myWorker.onmessage = function (e) {
        console.log(e);
        // sumTypeof();
      };
    };

    return {
      ...toRefs(types),
      ...toRefs(dropdown),
      store,
      typeSum,
      applyChanges,
      formatTypes,
    };
  },
};
</script>

